      -webkit-transform: translateX(-9px);
              transform: translateX(-9px);
      -webkit-transition: .3s;
      transition: .3s;
}
.mid .bg .triangle {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 13px 31px 0px 31px;
      border-color: rgba(105, 0, 209, 0.4) transparent transparent transparent;
      -webkit-transform: translateX(-20px);
              transform: translateX(-20px);
