            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            letter-spacing: 2px;
            font-size: 14px;
}
.mid .basics .skills .skill .val p i {
              font-size: 18px;
              margin: 0 3px;
}

